import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

import textField from 'muse-ui/textField';

import Api from '~/api';
import { ToastMsg } from '~/entity/toast';

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class Comp extends Vue {

    @Action('showToast')
    showToast;

    txt: string = null;

    get buttonEnable() {
        if (this.txt && this.txt.length > 0) {
            return true;
        } else {
            return false;
        }
    }

    activated() {
        this.txt = '';
    }
    
    handleMultiLineOverflow(over) {
        if (over) {
            this.txt = this.txt.slice(0, 298);
        }
    }

    async submit() {
        try {
            const res = await Api.feedback(this.txt);
            this.showToast(new ToastMsg('问题已提交，请等候客服人员联系您，谢谢'));
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }
}



